
.App {
  text-align: center;
  overflow: hidden;
}

a{
  text-decoration: none;
  color: white;
}

pre{
  padding: '0px 0px';
}

.images{
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 50%;
}

.page-footer{
  background-color: #373A47;
  color: #FFF;
  width: 100%;
  vertical-align: middle;
  text-align: center;
}

.footext{
  margin-top: 40px;
  vertical-align: middle;
}
#carousel-container {
  display: block;
  text-align: center;
  overflow: hidden;
  margin-left: auto; margin-right:auto;
  max-width: 100%;
}

#carousel-container img {
  text-align: center;
  margin-left: auto; margin-right:auto;
  display: block;
}

i {
  vertical-align: top;
  margin-right: 10px;
  padding-top: 0.8rem !important; padding-bottom: 5px !important;
}

iframe{
  align: center;
  border: 0;
  padding-bottom: 10px;
  padding-top: 10px;

  margin-right: auto;
  margin-right: center;
  max-width: 100%;
}

.link{
  padding-top: 10px;
  padding-bottom: 10px;

}

.link:hover{
  color:rgb(61, 150, 194);
}

.fa-github:hover{
  color: #100101;
}

.fa-youtube:hover{
  color: #FF0000;
}
.fa-fw{
  margin-right: 10px;
  margin-left: -10px;
}

h3{
  font-family: 'Open Sans';
  padding: '0px';
}

p, .components, .table {
    margin-top: 10px;
    font-size: calc(14px + (18 - 11) * ((100vw - 300px) / (1600 - 300)));
    font-family: 'Open Sans';
    text-align: justify;
}

.table{
  border: '2px';
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: rgb(61, 150, 194);
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: rgb(61, 150, 194);
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 1.5em 1.5em 0;
  font-size: 1.25em;
  overflow-y: hidden !important;

}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 1.2em;
  scroll-behavior: smooth;
  overflow: hidden !important;
  padding-top: 0.8rem !important; padding-bottom: 0.8rem !important;

}

#slideshow{
  text-align: center;
  max-width:540px;
  max-height:480px;
  width: auto;
  height: auto;
}
/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
  margin: 0; height: 100%; overflow: hidden;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



